<template>
	<view class="nav">
		<view class="content">
			<view class="nav-top">
				<input class="sousuo" placeholder="输入商品名称" type="search" confirm-type="search" @confirm="doSearch()" v-model="sousuocontent">
				<text class="iconfont icon-sousuo"></text>
			</view>
			<view class="nav-banner-img">
				<view class="nav-banner" :style="isTop==1?'position:stricky;background:#FFFFFF;z-index:9;top:0':''">
					<view class="nav-img" @click="change(0)">
						<image class="img-1" src="../../static/images/icons_21.gif"></image>
						<image class="img-0" v-bind:class="[0===typeindex?'activeimg':'']"
							src="../../static/images/icons_09.gif"></image>
						<view class="name" v-bind:class="[0===typeindex?'active':'']">推荐</view>
					</view>
					<view class="nav-img" @click="change(1)">
						<image class="img-1" src="../../static/images/icons_19.gif"></image>
						<image class="img-0" v-bind:class="[1===typeindex?'activeimg':'']"
							src="../../static/images/icons_05.gif"></image>
						<view class="name" v-bind:class="[1===typeindex?'active':'']">拿铁</view>
					</view>
					<view class="nav-img" @click="change(2)">
						<image class="img-1" src="../../static/images/icons_18.gif"></image>
						<image class="img-0" v-bind:class="[2===typeindex?'activeimg':'']"
							src="../../static/images/icons_03.gif"></image>
						<view class="name" v-bind:class="[2===typeindex?'active':'']">咖啡</view>
					</view>
					<view class="nav-img" @click="change(3)">
						<image class="img-1" src="../../static/images/icons_20.gif"></image>
						<image class="img-0" v-bind:class="[3===typeindex?'activeimg':'']"
							src="../../static/images/icons_07.gif"></image>
						<view class="name" v-bind:class="[3===typeindex?'active':'']">瑞纳冰</view>
					</view>
					<view class="nav-img" @click="change(4)">
						<image class="img-1" src="../../static/images/icons_22.png"></image>
						<image class="img-0" v-bind:class="[4===typeindex?'activeimg':'']"
							src="../../static/images/icons_11.gif"></image>
						<view class="name" v-bind:class="[4===typeindex?'active':'']">水果茶</view>
					</view>
				
				</view>
			</view>
			<view class="nav-box">
				<view class="nav-box-content" v-for="(item,index) in typelist" :key="index" @click="menu(index)">
					<view class="nav-img">
						<image :src="item.smallImg" class="img"></image>
					</view>
					<view class="nav-content">
						<view class="name">{{item.name}}</view>
						<view class="text">{{item.enname}}</view>
					</view>
					<view class="price">￥{{item.price}}</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import iconfont from '../../static/style/iconfont.css'
	export default {
		data() {
			return {
				type: "",
				typeindex: 0,
				typelist: '',
				typekey: "isHot",
				typevalue: '1',
				isTop: '',
				sousuocontent:''
			}
		},
		methods: {
			//商品类型
			async getRequest() {
				let res = await uni.request({
					url: 'http://www.kangliuyong.com:10002/type',
					data: {
						appkey: 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA='
					}
				})
				this.type = res[1].data.result
				console.log(this.type)
				if (this.typeindex > 0) {
					this.typevalue = this.type[this.typeindex - 1].type
					this.typekey = 'type'
				} else {
					this.typekey = "isHot",
						this.typevalue = '1'
				}
				//商品类型各各
				console.log(this.typevalue)
				let restype = await uni.request({
					url: "http://www.kangliuyong.com:10002/typeProducts",
					data: {
						appkey: "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=",
						key: this.typekey,
						value: this.typevalue
					}
				})
				console.log(restype)
				this.typelist = restype[1].data.result

			},
			change(index) {
				console.log(index)
				this.typeindex = index
				this.getRequest()
			},
			menu(index) {
				let r = this.typelist[index].pid
					uni.request({
						url: 'http://www.kangliuyong.com:10002/productDetail',
						data: {
							appkey: 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=',
							pid: r
						},
						success(res) {
							console.log(res.data.result[0])
							uni.setStorageSync('menulist', res.data.result[0])
							uni.navigateTo({
								url: '../menunew/menunew'
							})
						}
					})

			},
			//搜索
			doSearch(){
				uni.navigateTo({
					url:`../sousuo/sousuo?name=${this.sousuocontent}`,
					success() {
						
					}
				})
			}

		},
		onLoad() {
			this.getRequest()
		},
		onPageScroll: function(e) {

			if (e.scrollTop > 58) {
				this.isTop = 1
			} else {
				this.isTop = 0
			}
		}
	}
</script>

<style lang="less" scoped>
	.nav {
		font-size: 25rpx;

		.content {
			height: 1000rpx;

			.nav-top {

				margin: 30rpx;
				display: flex;
				position: relative;

				.icon-sousuo {
					position: absolute;
					color: #0c34ba;
					left: 30rpx;
					top: 18rpx;

				}

				.sousuo {
					color: #c8c9cc;
					width: 600rpx;
					height: 60rpx;
					text-align: center;
					border-radius: 30rpx;
					background-color: #f7f8fa;
					font-size: 20rpx;
				}
			}

			.nav-banner-img{
				height: 150rpx;
				overflow: hidden;
				.nav-banner {
					position: sticky;
					height: 166rpx;
					background-color: #fbfafa;
					display: flex;
					overflow-x: auto;
					.nav-img {
						width: 184rpx;
						position: relative;
						height: 150rpx;
						.img-0 {
							left: 0;
							width: 80rpx;
							height: 80rpx;
							margin-left: 62rpx;
							position: absolute;
						}
				
						.img-1 {
							width: 80rpx;
							height: 80rpx;
							margin-left: 62rpx;
						}
				
						.name {
							width: 208rpx;
							text-align: center;
							font-weight: bold;
						}
					}
				}
			}

			.nav-box {
				background-color: #f7f7f7;
				height: 100%;

				.nav-box-content {
					height: 240rpx;
					margin: 30rpx;
					display: flex;
					justify-content: flex-start;
					align-items: center;
					border-radius: 20rpx;
					background-color: #FFFFFF;

					.nav-img {
						.img {
							margin-left: 10rpx;
							width: 200rpx;
							height: 200rpx;
						}
					}

					.nav-content {
						margin-left: 30rpx;
						flex: 3;
						color: #808080;

						.name {

							margin-bottom: 10rpx;
						}
					}

					.price {
						margin-left: 60rpx;
						flex: 2;
						color: #0c34ba;
						font-weight: bold;
					}
				}
			}
		}

	}

	.active {
		color: #0a34ba;
	}

	.activeimg {
		visibility: hidden
	}
</style>
